<template>
    <div id="app">
        <!--loading-->
        <v-loading v-show="isLoadingShow"></v-loading>
        <!---->
        <v-header v-show="headerShow"></v-header>
        <!---->
        <v-nav v-show="navShow"></v-nav>
        <!---->
        <transition enter-active-class="animated bounceInUp">
            <router-view></router-view>
        </transition>
        <!---->
        <v-footer></v-footer>
        <!---->
        <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <v-playInfo v-if="isPlayInfoShow"></v-playInfo>
        </transition>
        <!---->
        <audio src="" ref="audio" autoplay="autoplay" @ended="loopPlayback" @timeupdate="getcurrentTime" @canplay="getDuration"></audio>
    </div>
</template>

<script>
    import {mapActions,mapGetters} from 'vuex'//mapActionsmapGetters
    import Header from './components/header.vue'
    import Footer from  './components/playerbar.vue'
    import NavBar from './components/navBar.vue'
    import PlayInfo from './components/playInfo.vue'

    export default {
        name: 'app',
        data () {
            return {
                headerShow:true,//
                navShow:true,//
            }
        },

        components:{
            'v-header':Header,
            'v-footer':Footer,
            'v-nav':NavBar,
            'v-playInfo':PlayInfo
        },

        mounted() {
            this.$store.dispatch('sendAudio',this.$refs.audio);//audioaction
            this.routerChange(this.$route.path);//
        },

        methods:{
            //babel
            //$ npm install babel-plugin-transform-object-rest-spread
            //babelrc
            //	"plugins": ["transform-object-rest-spread"]
            routerChange(path) {//
                path=path.substring(1);
                if (path.indexOf('toplist') !=-1) {
                    this.headerShow=false;
                    this.navShow=false;
                } else {
                    this.headerShow=true;
                    this.navShow=true;
                }
            },
            ...mapActions([
                'nextSong',
                'loopPlayback',//
                'getcurrentTime',//
                'getDuration'//
            ])
        },

        computed:mapGetters([
            'isLoadingShow',//loading
            'isPlayInfoShow'//
        ]),

        watch:{
            $route(to){//
                this.routerChange(to.path)
            }
        }
    }
</script>

<style lang="scss">

</style>



